<template>
  <div class="vx-row">
    <div
      style="margin: auto; width: 50%; padding: 10px"
      class="vx-col md:w-1/2 w-full mb-base"
    >
      <vx-card title="Formulaire d'ajout des utilisateurs" code-toggler>
        <div class="vx-row mb-2">
          <div class="vx-col w-full">
            <vs-input
              class="w-full"
              icon-pack="feather"
              icon="icon-user"
              icon-no-border
              label-placeholder="Nom"
              v-model="user.first_name"
              name="first_name"
              v-validate="'required'"
            />
            <span class="text-danger text-sm" v-show="errors.has('first_name')">{{ errors.first('first_name') }}</span>
          </div>
        </div>
        <div class="vx-row mb-2">
          <div class="vx-col w-full">
            <vs-input
              class="w-full"
              icon-pack="feather"
              icon="icon-user"
              icon-no-border
              label-placeholder="Pr√©nom"
              v-model="user.last_name"
              name="last_name"
              v-validate="'required'"
            />
            <span class="text-danger text-sm" v-show="errors.has('last_name')">{{ errors.first('last_name') }}</span>
          </div>
        </div>
        <div class="vx-row mb-2">
          <div class="vx-col w-full">
            <vs-input class="w-full" icon-pack="feather" icon="icon-user" icon-no-border label-placeholder="Nom d'utilisateur"
              v-model="user.username" v-validate="'required'" name="username" />
              <span class="text-danger text-sm" v-show="errors.has('username')">{{ errors.first('username') }}</span>
          </div>
        </div>
        <div class="vx-row mb-2">
          <div class="vx-col w-full">
            <vs-input
              type="email"
              class="w-full"
              icon-pack="feather"
              icon="icon-mail"
              icon-no-border
              label-placeholder="Email"
              v-model="user.email"
              name="email"
              v-validate="'required'"
            />
            <span class="text-danger text-sm" v-show="errors.has('email')">{{ errors.first('email') }}</span>
          </div>
        </div>
        <div class="vx-row mb-2">
          <div class="vx-col w-full">
            <vs-input
              type="password"
              class="w-full"
              icon-pack="feather"
              icon="icon-lock"
              icon-no-border
              label-placeholder="Mot de passe"
              v-model="user.password1"
              name="password1"
              v-validate="'required'"
            />
            <span class="text-danger text-sm" v-show="errors.has('password1')">{{ errors.first('password1') }}</span>
          </div>
        </div>
        <div class="vx-row mb-6">
          <div class="vx-col w-full">
            <vs-input
              type="password"
              class="w-full"
              icon-pack="feather"
              icon="icon-lock"
              icon-no-border
              label-placeholder="Confirmation mot de passe"
              v-model="user.password2"
              name="password2"
              v-validate="'required'"
            />
            <span class="text-danger text-sm" v-show="errors.has('password2')">{{ errors.first('password2') }}</span>

          </div>
        </div>
        <div class="vx-row mb-6">
          <div class="vx-col w-full">
            <vs-checkbox v-model="user.is_staff">Commercial</vs-checkbox>
          </div>
        </div>
        <div class="vx-row mb-6">
          <div class="vx-col w-full">
            <vs-checkbox color="success" v-model="user.is_superuser">Administrateur</vs-checkbox>
          </div>
        </div>
        <div class="vx-row">
          <div class="vx-col w-full">
            <vs-button class="mr-3 mb-2" @click="submitForm">Ajouter</vs-button>
            <vs-button
              color="warning"
              type="border"
              class="mb-2"
              @click="
              user = '';
              "
              >Reset</vs-button
            >
          </div>
        </div>
      </vx-card>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { Validator } from 'vee-validate';
const dict = {
  custom: {
    first_name: {
      required: 'champs requis',
    },
    last_name: {
      required: 'champs requis',
    },
    email: {
      required: 'champs requis',
    },
    username: {
      required: 'champs requis',
    },
    password1: {
      required: 'champs requis',
    },
    password2: {
      required: 'champs requis',
    },
    email: {
      required: 'champs requis',
    },
  }
};

// register custom messages
Validator.localize('en', dict);

export default {
  data() {
    return {
      user: {
        first_name: '',
        last_name: '',
        email: '',
        username: '',
        password1: '',
        password2: '',
        is_active: true,
        is_staff: false,
        is_superuser: false,
      },
    };
  },

  methods: {
    submitForm() {
      this.$validator.validateAll()
      .then((res)=> {
        if (res == true) {
          this.AddUser()
        }
      })
    },
    async AddUser() {
      await axios
        .post("/authentification/registration/",this.user, {
          headers: {
            Authorization: `Token ${localStorage.getItem("access_token")}`,
          },
        })
        .then(() => {
          this.$vs.notify({
            title: 'Ajout avec succ√®s',
            color: 'success'
          })
          this.$router.push('/utilisateurs')
        })
        .catch(() => {
          this.$vs.notify({
            title: "Erreur d'ajout",
            text: 'veuillez v√©rifier vos informations',
            iconPack: 'feather',
            icon: 'icon-alert-circle',
            color: 'danger'
          })
        });

    },
  },
};
</script>
